import { Component, OnDestroy, Input, SimpleChanges} from '@angular/core';

import { takeWhile } from 'rxjs/operators';
import { ChartsComponent } from '../charts.component';
import {AppComponent} from "../../../app.component"
import {SiteStatistic,SiteCollectionState,SiteCollectionConfiguration,SiteCollectionDashboard} from "../../../@core/data-services/globaldata.service"

import { timer } from 'rxjs';
import { notEqual } from 'assert';
import { utc } from 'moment';

@Component({
  selector: 'ngx-sitecollection-tile',
  styleUrls: ['./sitecollection-tile.component.scss'],
  templateUrl: './sitecollection-tile.component.html',
})


export class SiteCollectionTileComponent implements OnDestroy {  
  @Input() public SiteCollection: SiteCollectionDashboard;

  private alive = true;
  app:AppComponent;

  isOlder:boolean;
  
  progressInfoData: any;
  test:any;



  siteStatistics:SiteStatistic;
  configuration:SiteCollectionConfiguration;

  constructor(appComponents:AppComponent) {
    this.app=appComponents;

    this.progressInfoData=[
      { "title":"Loading data...","value":0,"activeProgress":0,"description":"..."}
    ];
   }
 
   ngOnInit() {
    console.log("Running for collection: ",this.SiteCollection.Name);
    this.app.globalDataService.getSiteStatistics.subscribe((res:SiteStatistic[])=>{      
      if (res.length !=0 && this.app.siteStatistics!=null) {
        //it's only for notification - using data from parent component
        // some othe updates
        this.siteStatistics=this.app.siteStatistics.find(x=> x.SiteCollection === this.SiteCollection.Name);
        this.configuration = this.app.configuration.SiteCollectionConfigurations.find(x=> x.Name === this.SiteCollection.Name);
        
        this.siteStatistics=this.app.siteStatistics.find(x=> x.SiteCollection === this.SiteCollection.Name);
        if (this.siteStatistics != null) {
          let maxWorker="unknown";
          let maxSessions="unknown";
          let CurrentWorkersActiveAzureProgress=0;
          let sessionCountProgress=0;
          let sessionsPerWorkerProgress=0;
          let note="";
          
          if (this.siteStatistics!=null) {
            CurrentWorkersActiveAzureProgress=(this.siteStatistics.CurrentWorkersActiveAzure+this.siteStatistics.CurrentWorkersActiveLocal)/this.siteStatistics.WorkerCount*100.0;
            maxWorker=this.siteStatistics.WorkerCount.toString();
          }
          if (this.configuration!=null) {
            if (this.siteStatistics!=null) {
              let workerCount=(this.siteStatistics.CurrentWorkersActiveAzure+this.siteStatistics.CurrentWorkersActiveLocal);//   this.workerStatistics.WorkerCount;
              let maxSessionsNb=workerCount*this.configuration.ScaleParameters.SessionsPerWorker;
              maxSessions=maxSessionsNb.toString()+" ("+workerCount.toString()+" worker)";
              sessionCountProgress=this.siteStatistics.LastReportedSessionsCountAll/maxSessionsNb*100.0;
            }
            sessionsPerWorkerProgress=this.siteStatistics.LastReportedSessionsCountAll/(this.siteStatistics.CurrentWorkersActiveAzure+this.siteStatistics.CurrentWorkersActiveLocal)/this.configuration.ScaleParameters.SessionsPerWorker*100;
            if (!this.configuration.ScaleParameters.Enabled) {
              note+="\nScaling of this Site Collection is disabled."
            }
          } else {
            note="\nThis Site Collection is not managed."
          }
          note+="\nLast updated on local time: "+this.siteStatistics.LastSessionDataLocal;
          var minutesMaxBack = new Date();
          minutesMaxBack.setMinutes(minutesMaxBack.getMinutes() - 3);
          
          if (this.siteStatistics.LastSessionDataUtc<minutesMaxBack) {
            this.isOlder=true;
          } else {
            this.isOlder=false;
          }

          this.progressInfoData=
            { "Title":this.siteStatistics.SiteCollection,
              "SessionCount":this.siteStatistics.LastReportedSessionsCountAll,
              "SessionCountProgress":sessionCountProgress,
              "MaxSessions":maxSessions,

              "WorkerActive":(this.siteStatistics.CurrentWorkersActiveAzure+this.siteStatistics.CurrentWorkersActiveLocal),
              "WorkerActiveProgress":CurrentWorkersActiveAzureProgress,
              "MaxWorker":maxWorker,
              
              "SessionsPerWorker":this.siteStatistics.LastReportedSessionsCountAll/(this.siteStatistics.CurrentWorkersActiveAzure+this.siteStatistics.CurrentWorkersActiveLocal),
              "SessionsPerWorkerProgress":sessionsPerWorkerProgress,
              "Note":note,
              
              "description":"Session count"};
        }
      } else {
        console.log("Not all data ready to display data. Maybe StatisitcData is missing. Skipping view for now.")
      }
    });
  }
  ngOnDestroy() {
    // this.alive = true;
  }
}
