import { Component, OnInit } from '@angular/core';
import {ChartsComponent} from '../charts.component';
import {AppComponent} from "../../../app.component"
import { NbThemeService, NbColorHelper } from '@nebular/theme';

import {GlobalDataService,SiteStatistic,SiteCollectionState,Configuration,SiteCollectionDashboard,SiteCollectionStatesCollection} from "../../../@core/data-services/globaldata.service"

@Component({
  selector: 'ngx-overview-charts',
  templateUrl: './overview-charts.component.html',
  styleUrls: ['./overview-charts.component.scss']
})
export class OverviewChartsComponent implements OnInit {
  data: any;
  options: any;
  colors: any;
    
  loading: boolean;
  chartjs: any;
  chartDelay:number;
  themeSubscription: any;
  timerSubscription: any;

  chartsComponent:any;
  app:any;

  configuration:Configuration;
  selectedSiteCollection:string="";
  siteCollectionDashboardsToDisplay:SiteCollectionDashboard[]=null;
  //siteCollectionStatesCollection:SiteCollectionStatesCollection[]=null;


  toggleLoadingAnimation() {
    this.loading = true;
    setTimeout(() => this.loading = false, 3000);
  }

  constructor(chartsComponent:ChartsComponent,appComponent:AppComponent, private theme: NbThemeService) { 
    this.chartsComponent=chartsComponent;
    this.app=appComponent;
    this.themeSubscription = this.theme.getJsTheme();
    this.themeSubscription = this.theme.getJsTheme().subscribe(config => {
      this.colors = config.variables;
      this.chartjs = config.variables.chartjs;
    });

  }

  ngOnInit() {
    this.chartDelay=1000;
    this.setOptions(this.chartDelay);
    this.siteCollectionDashboardsToDisplay=this.chartsComponent.siteCollectionDashboardsToDisplay;
    if (this.siteCollectionDashboardsToDisplay.length>0) {
      this.onChange_selectedSiteCollection(this.siteCollectionDashboardsToDisplay[0].Name);
    }
    this.app.globalDataService.getConfiguration.subscribe((res:Configuration)=>{
      if (res!=null) {
        this.configuration=res;
      }
    })
    this.loading = true;
    this.app.globalDataService.getSiteCollectionStatesRunning.subscribe((res:SiteCollectionStatesCollection[])=>{
      // incomming  data
      console.log("Incomming data");
      this.loading=false;
      if (res!=null) {
        let data=res.find(n=>n.SiteCollection==this.selectedSiteCollection).SiteCollectionStates;
        let currentConfiguration=this.configuration.SiteCollectionConfigurations.find(n=>n.Name==this.selectedSiteCollection);
        let sessionsPerWorker=1;
        if (currentConfiguration!=null) {
          sessionsPerWorker=currentConfiguration.ScaleParameters.SessionsPerWorker;
        }
        this.setOptions(this.chartDelay);
        //
        // todo: order by local time
        // worker*sessions per worker from configuration
        // make a function and call it after data update and change of siteCollection
        // check if data array empty
        let labels = [];
        let data1 = [];
        let data2 = [];
        let data3 = [];
        let data4 = [];
        data.forEach((x) => {
          labels.push(x.LocalTime);
          data1.push(x.WorkerActive*sessionsPerWorker);
          data2.push(x.SessionCountAll);
          data3.push(x.SessionCountConnected);
          //data1.push(x.AllUsers);
        });
        this.data = {
          labels: labels,
          datasets: [{
            data: data1,
            label: "Worker capacity",
            backgroundColor: NbColorHelper.hexToRgbA(this.colors.primary, 0.3),
            borderColor: this.colors.primary,   
            borderWidth: 1
          },
          {
            data: data2,
            label: "Session count - All",
            backgroundColor: NbColorHelper.hexToRgbA(this.colors.danger, 0.3),
            borderColor: this.colors.danger,
            borderWidth: 0.8
          },
          {
            data: data3,
            label: "Session count - Active",
            backgroundColor: NbColorHelper.hexToRgbA(this.colors.info, 0.3),
            borderColor: this.colors.info,
            borderWidth: 0.5
          }
        ],
        };
        this.chartDelay=0;

        //
      }
      // delete this later with the json view
      //this.siteCollectionStatesCollection=res;
    })
  }
  public onChange_selectedSiteCollection(siteCollection:string) {
    this.selectedSiteCollection=siteCollection;
    // this.data = {
    //   labels: "null",
    //   datasets: [{
    //     data: [9],
    //     label: "Loading data ...",
    //     backgroundColor: NbColorHelper.hexToRgbA(this.colors.primary, 0.3),
    //     borderColor: this.colors.primary,   
    //     borderWidth: 1
    //   }]};
    console.log("SiteCollection selection changed");
    this.loading=true;
    this.chartDelay=1000;
    this.app.globalDataService.initSiteCollectionStatesRunning(this.selectedSiteCollection);
  }
  
  private setOptions(animationDuration:number) {
    var colors = require('nice-color-palettes');
    this.options = {
      elements: { point: { radius: 0 } },
      animation: { duration: animationDuration },
      responsive: true,
      maintainAspectRatio: false,
      scales: {
        xAxes: [
          {
            gridLines: {
              display: true,
              color: this.chartjs.axisLineColor,
            },
            ticks: {
              fontColor: this.chartjs.textColor,
            },
          },
        ],
        yAxes: [
          {
            gridLines: {
              display: true,
              color: this.chartjs.axisLineColor,
            },
            ticks: {
              fontColor: this.chartjs.textColor,
            },
          },
        ],
      },
      legend: {
        labels: {
          fontColor: this.chartjs.textColor,
        },
      },
    };
  }


}
