import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ChartsComponent } from './charts.component';
import { ChartjsComponent } from './chartjs/chartjs.component';
import { OverviewTilesComponent } from './views/overview-tiles.component'
import { OverviewChartsComponent } from './views/overview-charts.component'

const routes: Routes = [{
  path: '',
  component: ChartsComponent,
  children: [{
    path: 'tiles',
    component: OverviewTilesComponent
  },
  {
    path: 'charts',
    component: OverviewChartsComponent
  }],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ChartsRoutingModule { }

export const routedComponents = [
  ChartsComponent,
  OverviewTilesComponent,
  ChartjsComponent,
];
