import { Component, OnDestroy } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import {ConfigurationDataService} from "../../../@core/data-services/configuration.service"
import { ChartsComponent } from '../charts.component';
import {AppComponent} from "../../../app.component"

@Component({
  selector: 'ngx-Command',
  template: `{{data|json}}
}}data.command{{ <button nbButton style="float: right;" size="xsmall" (click)="runEngine()" title="Start enginge manually"><i class="fa"  aria-hidden="true">&#x25B6;</i></button>
    
  `,
  providers: [ConfigurationDataService]
})
export class CommandComponent implements OnDestroy {
  data: any;
  options: any;
  themeSubscription: any;
  private configurationDataService: ConfigurationDataService;
  private chartsComponent:ChartsComponent;
  private app:AppComponent;

 // constructor(private theme: NbThemeService, configurationDataService:ConfigurationDataService) {
 //   this.configurationDataService=configurationDataService;
 // }
  constructor(private theme: NbThemeService,chartsComponent:ChartsComponent,appComponents:AppComponent) {
   this.chartsComponent=chartsComponent;
   this.app=appComponents;

 }

  runEngine() {
    this.data.command="RunNow";
    this.configurationDataService.setCommand(this.data);
  }

  ngOnInit() {
    this.app.globalDataService.getCommand.subscribe(res=>{
      this.data=res as any;
    });
    // this.configurationDataService.getCommand.subscribe(res=>{
    //   this.data=res as any;
    // });
  }

  ngOnDestroy(): void {
    //this.themeSubscription.unsubscribe();
  }
}
