import { Component } from '@angular/core';
import {AppComponent} from "../../app.component"

import {GlobalDataService,SiteStatistic,SiteCollectionState,Configuration,SiteCollectionDashboard,SiteCollectionStatesCollection} from "../../@core/data-services/globaldata.service"

@Component({
  selector: 'ngx-charts',
  template: `<router-outlet></router-outlet>`,
  providers: [GlobalDataService]
})
export class ChartsComponent {  
  app:AppComponent;

  siteStatistics:SiteStatistic[];
  siteCollectionDashboards:SiteCollectionDashboard[]=[];
  siteCollectionDashboardsToDisplay:SiteCollectionDashboard[]=null;
  
  siteCollectionStatesCollection:SiteCollectionStatesCollection[]=[];

  constructor(appComponents:AppComponent) {
    this.app=appComponents;
  }
 
  ngOnInit() {
    this.app.globalDataService.getSiteStatistics.subscribe((res:SiteStatistic[])=>{
      if (res!=null && this.app.configuration!=null) {
        this.siteStatistics=res;
        this.siteStatistics.forEach((item, index) => {
          let config = this.app.configuration.SiteCollectionConfigurations.find(x=> x.Name === item.SiteCollection);
          let siteCollectionDashboard:SiteCollectionDashboard={
            Index: index,
            IsManaged: true,
            Name: item.SiteCollection,
            SortIndex:item.LastReportedSessionsCountAll
          }
          if (config==null) {
            siteCollectionDashboard.IsManaged=false;
          }
          let siteCollectionDashboardsIndex=this.siteCollectionDashboards.findIndex(x=> x.Name === item.SiteCollection);
          if (siteCollectionDashboardsIndex>-1) {
            this.siteCollectionDashboards[siteCollectionDashboardsIndex]=siteCollectionDashboard;
          } else {
            this.siteCollectionDashboards.push(siteCollectionDashboard);
          }
        });
        this.siteCollectionDashboards=this.siteCollectionDashboards.sort((x,y) => x.SortIndex > y.SortIndex ? -1 : 1).sort((x,y) => x.IsManaged > y.IsManaged ? -1 : 1);
        for (let index = 0; index < this.siteCollectionDashboards.length; index++) {
          this.siteCollectionDashboards[index].Index=index;
          
        }
        if (this.siteCollectionDashboardsToDisplay==null) {
          this.siteCollectionDashboardsToDisplay=this.siteCollectionDashboards;
        }
        console.log("siteCollectionDashboardsToDisplay:",this.siteCollectionDashboards);
      }
    });
  }
  
}
